import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import Config from './page/Config';
import MultiContentList from './page/MultiContentList';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedContentName: null,
            mappingOfContentTemplate: [],
            contentIdAndTemplateId: ""
        };
    }

    setContentTemplate = (data) => {
        const getMappingToEndcode = JSON.stringify(data.map(el => ({templateId: el.templateId, contentId: el.contentId})));
        this.setState({ mappingOfContentTemplate: data, contentIdAndTemplateId: encodeURI(getMappingToEndcode) });
    }

    setSelectedContentName = (contentName) => {
        this.setState({ selectedContentName: contentName })
    }

    render() {
        console.log('MAIN-STATE', this.state)
        return (
            <>
                <HashRouter>
                    <Switch>
                        <Route path='/' exact>
                            <Config
                                selectedContent={this.state.mappingOfContentTemplate}
                                mappingOfContentTemplate={this.state.mappingOfContentTemplate}
                                setContentTemplate={this.setContentTemplate}
                            />
                        </Route>
                        <Route path='/configpage' exact>
                            <MultiContentList 
                                setContentTemplate={this.setContentTemplate}
                                setSelectedContentName={this.setSelectedContentName} />
                        </Route>
                    </Switch>
                </HashRouter>
            </>
        )
    }
}
export default App;